    }
  
  
  .arrow {
  
    margin: 0 auto;
  
    margin-top: 5px;
  
    width: 15px;
  
    height: 15px;

    color: #ECE9D6;
  
    background-image: url(data:image/svg+xml;base64,);
  
    background-size: contain;
  
    }
  
  
  .bounce {
  
    -webkit-animation: bounce 4s infinite;
  
    animation: bounce 4s infinite;
  
    }
